


















import Vue from 'vue'
export default Vue.extend({
  props: {
    name: {
      type: String,
      required: true
    },
    set: {
      type: Set,
      required: true
    },
    currentEntry: {
      type: String,
      required: true
    }
  },

  computed: {
    entries () {
      const data = [...this.set.values()] as string[]
      return data
    }
  },

  mounted() {
    console.log(this.name)
    console.log(this.set)
  },

  watch: {
    
  }
})
